.breed {
  background-color: transparent;
  padding: 32px;
  padding: 2rem;

  &Search {
    height: auto;
    vertical-align: top;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: horizontal;
    -webkit-box-direction: normal;
        -ms-flex-direction: row;
            flex-direction: row;
    -webkit-box-pack: justify;
        -ms-flex-pack: justify;
            justify-content: space-between;
    max-width: 800px;
  }
}

@media (max-width: 767px) {

  .breed {

    &Search {
      -webkit-box-orient: vertical;
      -webkit-box-direction: normal;
          -ms-flex-direction: column;
              flex-direction: column;
    }
  }
}

.breed {

  &Search {

    &Select {
      -webkit-box-flex: 5;
          -ms-flex: 5 1 0px;
              flex: 5 1 0;
    }

    &Input {
      -webkit-box-flex: 5;
          -ms-flex: 5 1 0px;
              flex: 5 1 0;
    }

    &Button {
      -webkit-box-flex: 1;
          -ms-flex: 1 1 0px;
              flex: 1 1 0;
    }
  }
}

@media (max-width: 767px) {

  .breed {

    &Show {
      max-height: 300px;
    }
  }
}

@media (max-width: 1024px) {

  .breed {

    &Show {
      padding: 16px 0;
      padding: 1rem 0;
      max-height: 400px;
    }
  }
}

.breed {

  &Show {
    width: 100%;
    max-width: 800px;
    height: 500px;
    max-height: 500px;
    position: relative;
  }
}
