.button {
  width: 100%;
  background-color: #aa418c;
  -webkit-box-shadow: 0 4px 6px rgba(190, 190, 190, .2), 0 1px 3px rgba(190, 190, 190, .2);
          box-shadow: 0 4px 6px rgba(190, 190, 190, .2), 0 1px 3px rgba(190, 190, 190, .2);
  border-radius: 0.25rem;
  border: 1px solid #aa418c;
  border: 0.0625rem solid #aa418c;
  color: #fff;
  cursor: pointer;
  display: inline-block;
  font-size: 18px;
  font-weight: 500;
  line-height: 16px;
  line-height: 1rem;
  min-width: 80px;
  min-width: 5rem;
  padding: 16px 24px;
  padding: 1rem 1.5rem;
  text-decoration: none;
  -webkit-transition: all 0.15s ease;
  transition: all 0.15s ease;
  white-space: nowrap;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}
